body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000000;
  color:#ffffff;
}


header {
  
  flex-direction: column;
  align-items: left;
  justify-content: center;
  color: white;
  margin: 10px;
  padding:10px;
}

p{
  text-align: justify;
  margin:10px;
}

.title{
  color:#4e535c;
}

#my-id{
  text-decoration: underline;
}

.marvin{
height:300px;
  float:left;
  clear:both;
  margin : 10px;
}



#toc_container {
  text-align: left;
  border: 1px solid #aaa;
  display: table;
  margin:auto;
  padding: 20px;
  width: auto;
}

.toc_title {
  font-weight: 700;
  text-align: center;
}

#toc_container li, #toc_container ul, #toc_container ul li{
  list-style: outside none none !important;
}

h3{
  text-decoration: underline;
  margin-top:25px;
}

a{
  margin:10px;
}

.command{
  background-color: #4e535c;
  padding:2px;
  padding-left: 5px;
  padding-right: 5px;
  border-radius: 3px;
  margin: 10px;
  width: fit-content;
  display: block; 
  line-height: 20px;
  font-size: 16px;;

}